let SlidersArray = []
function sliderAdvantage() {
    $(".advantage-card2__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.advantage-card2__slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 10,
            pagination: {
                el: `.advantage-card2__slider[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true
            },
        })
    })
}

function sliderQuestions() {
    var swiper = new Swiper('.questions .mySwiper', {
        slidesPerView: '3',
        spaceBetween: 64,
        pagination: {
            el: ".questions .swiper-pagination",
            clickable: true
        },
        breakpoints: {
            320: {
                slidesPerView: '1.3',
                spaceBetween: 40,
            },
            769: {
                slidesPerView: '2',
                spaceBetween: 40,
            },
            993: {
                slidesPerView: '3',
                spaceBetween: 40,

            },
            1621: {
                slidesPerView: '3',
                spaceBetween: 64,
            }
        }
    })
}

$(document).ready(function () {
    sliderAdvantage();
    sliderQuestions();

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__menu').toggleClass('header__menu--active')
        $('body').toggleClass('fixed-body')
    })

    $('.header__list li a').click(function () {
        $('.burger').removeClass('burger--active')
        $('.header__menu').removeClass('header__menu--active')
        $('body').removeClass('fixed-body')
    })

    //Video
    $('.video__group').each(function() {
        const $video = $(this).find('video');
        const $playButton = $(this).find('.video__play');
        const $videoTitle = $(this).find('.video__title');
        const video = $video[0];

        $(this).on('click', function(event) {
            event.preventDefault();

            if (video.paused) {
                video.play();
                $playButton.addClass('hidden');
                $videoTitle.addClass('hidden')
                video.controls = true;
            } else {
                video.pause();
            }
        });
    });

    //Hover-slider
    $(".hover__pag-item").mouseover(function() {
        let path = $(this).attr("data-tab-path")
        $(this).parents(".hover__slide").find(".hover__pag-item").removeClass("hover__pag-item--active")
        $(this).addClass("hover__pag-item--active")
        $(this).parents(".hover__slide").find(".hover__img").removeClass("hover__img--active")
        $(this).parents(`.hover__slide`).find(`.hover__img[data-tab-path="${path}"]`).addClass("hover__img--active")
        $(this).parents(".hover__slide").find(".pagination-bullet").removeClass("pagination-bullet--active")
        $(this).parents(`.hover__slide`).find(`.pagination-bullet[data-tab-path="${path}"]`).addClass("pagination-bullet--active")
    })

    //Cookies
    $('.cookies__close').click((function () {
        $(this).parents('.cookies').remove()
    }))

    //Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    function closeModal() {
        $('.modal--active video').each(function () {
            this.pause();
            this.currentTime = 0;
        });

        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})